



























$prefixCls: "vue-sound";
.#{$prefixCls}-wrapper {
  .#{$prefixCls}__player {
    height: 30px;
    line-height: 30px;

    a {
      cursor: pointer;
      display: inline-block;
      vertical-align: middle;
    }

    .#{$prefixCls}__extern-wrapper {
      display: inline-block;
      margin-left: 10px;

      > a {
        margin-right: 5px;
        vertical-align: 0;
      }

      .volume-toggle {
        position: relative;

        .volume-slider {
          cursor: pointer;
          position: absolute;
          top: 0;
          width: 80px;
          left: 24px;
        }
      }
    }

    .#{$prefixCls}__playback-time-wrapper {
      background: transparent;
      position: relative;
      width: 250px;
      display: inline-block;
      background: #e0dcd7;
      height: 3px;
      cursor: pointer;
      font-size: 14px;
      vertical-align: middle;

      .#{$prefixCls}__playback-time-separator::after {
          content:' : ';
      }

      .#{$prefixCls}__playback-time-total {

      }

      .#{$prefixCls}__playback-time-indicator {
          background: #5099ff;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
      }
    }
  }
}
